import { reactive, toRefs } from 'vue'
import util from "@/util"
import Laboratory from '@/Laboratory'

const state = reactive({
  loading: true,
  //researchInfo: reactive({}),
  researchTreeString: "",
  liliana: "",
  debug: false
})

export default function globalState() {
  const load = async () => {
    state.loading = true
    let researchTree = await util.fetchHjson('research/research-tree.hjson')
    for (let node of researchTree.nodes) {
      node.thumb = 'research/' + node.thumb
      //console.log(node.thumb)
    }
    state.researchTreeString = JSON.stringify(researchTree)
    state.liliana = JSON.stringify(await util.getInfo('liliana'))
    state.loading = false
  }

  return { ...toRefs(state), load }
}
