<template>
  <div class="farm-window funds-card" :style="cssVars">
    <h2>Income Statement</h2>
    <p><i>This statement assumes that milk is harvested optimally.</i></p>
    <div class="farm-list">
      <div class="summary-card">
        <h3>Milk</h3>
        <h4>Price</h4><p>{{ farm.milkprice.toFixed(0) }}</p> <p> $ / L</p><p></p>
        <h4>Production</h4><p>{{ (farm.production * 60).toFixed(1) }}</p><p> L / Day</p><p></p>
        <h4>Profit</h4><p>{{ (farm.productionValue * 60).toFixed(0) }}</p><p> $ / Day</p><p></p>
      </div>

      <div class="summary-card">
        <h3>Salaries</h3>
        <!--<p>Total: ${{ (-farm.currentSalaries * 60).toFixed(0) }} / Day</p>-->
