          <h4>{{ employee.name }}</h4><p>{{ (-employee.salary.value * 60).toFixed(0) }}</p><p>$ / Day</p>
          <div class="option-buttons">
            <TextButton class="farm-button" @click="employee.salary.baseValue -= 0.1">
              <p>-</p>
            </TextButton>
            <TextButton class="farm-button" @click="employee.salary.baseValue += 0.1">
              <p>+</p>
            </TextButton>
          </div>
        </template>
        <h4>Sum</h4><p>{{ (-farm.currentSalaries * 60).toFixed(0) }}</p><p>$ / Day</p><p></p>
      </div>

      <div class="summary-card">
        <h3>Total</h3>
        <h4>Cashflow</h4><p>{{ ((Math.round(farm.productionValue * 60) - Math.round(farm.currentSalaries * 60))) }}</p> <p> $ / Day</p><p></p>
      </div>
    </div>

  </div>
</template>

