<template>
  <div v-if="isLoaded" class="page-grid">
    <StatusBar :farm="farm"></StatusBar>
    <div class="selected-staff">
      <EmployeeCard v-if="farm.employeeById(selectedEmployeeId) !== undefined"
                    :employee="farm.employeeById(selectedEmployeeId)"
                    :farm="farm"
                    ondragstart="return false"
      />
      <div v-else class="no-selected">
        <h1>Select an employee</h1>
      </div>
    </div>
  </div>
  <div class="staff-list">
    <template v-for="employee in farm.state.employees" v-bind:key="employee">
      <EmployeeCardSmall :image="employee.image"
                         :title="employee.name"
                         @click="select(employee)"
                         :class="{'selected-employee': selectedEmployeeId === employee.uuid}"
                         ondragstart="return false"
      />
    </template>
    <div class="farm-window hire-new" @click="hireNew()" title="Hire another employee">
      <div class="plus-horizontal"></div>
      <div class="plus-vertical"></div>
      <div><p>${{ farm.hiringCost() }}</p></div>
    </div>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import Farm from '@/Farm'
import EmployeeCard from '@/components/EmployeeCard.vue'
import ValueMeter from '@/components/ValueMeter.vue'
import EmployeeCardSmall from '@/components/EmployeeCardSmall.vue'
import Research from '@/Research'
import Employee from '@/employee/Employee'
import FarmStatusCard from '@/components/FarmStatusCard.vue'
import StatusBar from '@/components/StatusBar.vue'

@Options({
  components: {
    EmployeeCard, EmployeeCardSmall, ValueMeter, FarmStatusCard, StatusBar
  },
  props: {
    farm: Object
  },
  methods: {
    log(msg: any) {
      console.log(msg)
    },
    select(e: Employee) {
      this.selectedEmployeeId = e.uuid
    },
    async hireNew() {
      await this.farm.hire()
      this.select(this.farm.state.employees[this.farm.state.employees.length - 1])
    }
  },
  computed: {
  },
  async created() {
    //let farm = new Farm()
    //await farm.load()
    //this.farm = farm
    this.isLoaded = true
  }
})
export default class StaffView extends Vue {
  isLoaded = false
  farm!: Farm
  selectedEmployeeId = ''
}
</script>

