<template>
  <div class="farm-window research-card">
    <img :src="research.image"/>
    <div class="info-panel">
      <h3>{{ research.name }}</h3>
      <div class="option-buttons">
        <p><b>Target:</b></p> <p :title="targetTooltip()">{{ research.displayTarget }}</p>
        <p><b>Duration:</b></p> <p :title="permanenceTooltip()">{{ research.displayPermanence }}</p>
        <p><b>Effect:</b></p><p :title="durationTooltip()">{{ research.displayDuration }}</p>
      </div>
      <p>{{ research.description }}</p>
    </div>
    <div class="status-panel">
      <TextButton @click="farm.research(farm.state.lab.researchById(research.id))"
                  class="farm-button"
                  :class="{'disabled-button': research.isCompleted || research.isBeingResearched}">
        <p v-if="!research.isStarted">Start Research ${{ lab.getResearchCost(research).toFixed(0) }}</p>
        <p v-else-if="!research.isBeingResearched">Continue Research</p>
        <p v-else-if="research.isCompleted">Completed</p>
        <p v-else>Researching...</p>
      </TextButton>
      <ValueMeter :container="research.completion">
        <p class="meter-label">{{ research.completion.value.toFixed(0) }} / {{ research.completion.max.toFixed(0) }} Science</p>
      </ValueMeter>
    </div>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import Research from '@/Research'
import ValueMeter from '@/components/ValueMeter.vue'
import TextButton from '@/components/TextButton.vue'
import Farm from '@/Farm'

@Options({
  components: {
    ValueMeter, TextButton
  },
  props: {
    research: Research,
    farm: Object
  },
  methods: {
    log(msg: any) {
      console.log(msg)
    },
    targetTooltip() {
      if (this.research.effectType.target === 'farm') {
        return 'When researched, this will produce an upgrade for the farm.'
      } else if (this.research.effectType.target === 'employee') {
        return 'When researched, this will produce an item or effect for your employees.'
      }
      return ''
    },
    permanenceTooltip() {
      if (this.research.effectType.permanence === 'permanent') {
        return 'If use of the research is halted, effects already incurred are permanent.'
      } else if (this.research.effectType.permanence === 'temporary') {
        return 'If use of the research is halted, all effects will dissipate.'
      }
      return ''
    },
    durationTooltip() {
      if (this.research.effectType.timeType === 'instant') {
        return 'When used, all effects of this research will be applied instantly.'
      } else if (this.research.effectType.timeType === 'slow') {
        return 'When used, effects of this research will take some time before reaching full intensity.'
      }
      return ''
    }
  },
  computed: {
    lab() {
      return this.farm.state.lab
    }
  }
})
export default class ResearchCard extends Vue {
  isLoaded = false
  research!: Research
  farm!: Farm
}
</script>

