<template>
  <div class="milkfarm" v-if="isLoaded">
    <div class="menu">
      <TextButton @click="selectedTab='Menu'; farm.stopUpdating();"
                  class="tab menu-tab"
                  :class="{'selected-tab': selectedTab === 'Menu'}">
        <h1>Menu</h1>
      </TextButton>
      <TextButton v-for="tab in tabs" :key="tab" @click="selectTab(tab)"
                  class="tab"
                  :class="{'selected-tab': selectedTab === tab.id, 'disabled-tab': !saveSystem.hasFarm}">
        <h1>{{ tab.label }}</h1>
      </TextButton>
    </div>
    <keep-alive>
      <component :is="selectedTab" v-bind="currentProperties"></component>
    </keep-alive>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import Farm from '@/Farm'
import ValueMeter from '@/components/ValueMeter.vue'
import StaffView from '@/components/StaffView.vue'
import ResearchView from '@/components/ResearchView.vue'
import TextButton from '@/components/TextButton.vue'
import globalState from '@/store/globalState'
import { unref } from 'vue'
import Laboratory from '@/Laboratory'
import Office from '@/components/Office.vue'
import Menu from '@/components/Menu.vue'
import SaveSystem from '@/SaveSystem'

@Options({
  components: {
    ValueMeter, StaffView, ResearchView, AdminView: Office, TextButton, Menu
  },
  props: {
  },
  methods: {
    log(msg: any) {
      console.log(msg)
    },
    selectTab(tab: any) {
      if (!this.saveSystem.hasFarm) return
      this.selectedTab = tab.id
      this.farm.startUpdating()
    }
  },
  computed: {
    currentProperties: function() {
      if (this.selectedTab === 'StaffView') {
        return { farm: this.farm }
      }
      if (this.selectedTab === 'ResearchView') {
        return { farm: this.farm }
      }
      if (this.selectedTab === 'AdminView') {
        return { farm: this.farm }
      }
      if (this.selectedTab === 'Menu') {
        return { saveSystem: this.saveSystem }
      }
    },
    farm: function (): Farm {
      return this.saveSystem.farm
    }
  },
  async created() {
    await globalState().load()
    this.saveSystem = new SaveSystem()
    //let farm = new Farm()
    //this.farm = farm
    this.isLoaded = true
  }
})
export default class Milkfarm extends Vue {
  isLoaded = false
  saveSystem!: SaveSystem
  //farm!: Farm
  tabs = [
    { id: 'AdminView', label: 'Office' },
    { id: 'StaffView', label: 'Pasture' },
    { id: 'ResearchView', label: 'Lab' }
  ]

  selectedTab: string = 'Menu'
}
</script>

