import {identifier, serializable} from "serializr";
import util from "@/util";
import Farm from "@/Farm";

export default class FarmEvent {
  @serializable(identifier())
  private readonly uuid: string = util.uuid()

  // Describes the event. If text contains '{variable}', that will be substituted by 'args.variable' from the arguments.
  @serializable
  public text: string

  // When the event happened, in game time.
  @serializable
  public time: number = 0


  // Variables that only matter on event creation

  // A function. Takes an argument object. Can use this object to produce side-effects.
  private action: any
  // A function. Takes an object containing a farm. Returns an object whose keys will be passed as arguments to action.
  private argsFun: any

  constructor(text: string, action: any, argsFun: any = () => {}) {
    this.text = text
    this.action = action
    this.argsFun = argsFun
  }

  private substitute(text: string, args: any): string {
    for (let key in args) {
      text = text.replace(`{${key}}`, args[key])
    }
    return text
  }

  takeAction(farm: Farm){
    let args = this.argsFun({farm: farm})
    args.farm = farm
    this.time = farm.state.time
    this.text = this.substitute(this.text, args)

    this.action(args)
  }

  static milkPriceChangeEvent: () => FarmEvent = () => new FarmEvent("Price of milk has {text}!",
    (args: any) => {
      args.farm.state.milkPrice.addValue(args.value)
    }, (args: any) => {
      let value = Math.round((args.farm.state.milkPrice.value / 5) * (Math.random() - 0.5))
      let text = ""
      if (value > 0) text = `increased by $${value}`
      else if (value < 0) text = `decreased by -$${-value}`
      else text = `remained unchanged`
      return {
        value: value,
        text: text
      }
    })
}
