import { list, object, serializable } from "serializr"
import Research from "@/Research"
import Farm from "@/Farm"
import Employee from "@/employee/Employee"

export class ResearchItem {
  private employee: Employee

  @serializable(object(Research))
  public readonly research: Research

  @serializable
  private _isEquipped: boolean = false

  constructor(research: Research, employee: Employee) {
    this.employee = employee
    this.research = research
  }

  load(state: any) {
    this._isEquipped = state._isEquipped
  }

  toggleEquip() {
    if (this.isEquipped) {
      this.unequip()
    } else {
      this.equip()
    }
  }

  equip() {
    this._isEquipped = true
    this.employee.effectManager.addResearch(this.research, this.employee.effectToModsMap)
  }

  unequip() {
    if (!this.research.isUnequippable) return

    this._isEquipped = false
    this.employee.effectManager.removeResearch(this.research, this.employee.effectToModsMap)
  }

  get isEquipped() {
    return this._isEquipped
  }
}

export class Inventory {
  private farm: Farm
  private employee: Employee

  @serializable(list(object(ResearchItem)))
  private items: ResearchItem[] = []

  constructor(farm: Farm, employee: Employee) {
    this.farm = farm
    this.employee = employee
  }

  update(dt: number) {

  }

  addResearch(research: Research) {
    this.items.push(new ResearchItem(research, this.employee))
  }

  load(state: any) {
    for (let item of state.items) {
      let research = this.farm.state.lab.researchById(item.research.id)
      if (research == undefined) continue

      let newItem = new ResearchItem(research, this.employee)
      newItem.load(item)
      this.items.push(newItem)
    }
  }

  get allItems(): ResearchItem[] {
    return this.items
  }
}
