<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import Employee from '@/employee/Employee'
import Farm from '@/Farm'
import ValueMeter from '@/components/ValueMeter.vue'
import TextButton from '@/components/TextButton.vue'
import util from '@/util'

@Options({
  components: {
    ValueMeter, TextButton
  },
  props: {
    employee: Employee, farm: Farm
  },
  methods: {
    log(msg: any) {
      console.log(msg)
    },
    deltaSymbol(value: number): string {
      if (value > 0) return 'Increasing'
      else if (value < 0) return 'Decreasing'
      return 'Stable'
    }
  },
  computed: {
    cssVars() {
      return {
        '--background-image': 'url(' + require('@/assets/wolfgang-hasselmann.jpg') + ')'
      }
    },
    util() {
      return util
    }
  }
})
