import CollectionPlayer from "@/employee/CollectionPlayer"
import util from "@/util"
import { list, map, primitive, serializable } from "serializr"

export default class EffectType {
  @serializable
  public readonly target: string // employee, farm

  // Temporary: Effect can be removed
  // Permanent: Effect can not be removed by being "unequipped"
  @serializable
  public readonly permanence: string // temporary, permanent

  // Instant: Effect is applied immediately and is removed after duration finishes
  // Slow: Effect value ramps up gradually over the duration
  @serializable
  public readonly timeType: string // instant, slow

  // How long the effect will last (depends on what other types effect is)
  @serializable
  public readonly duration: number

  constructor(duration: number = 0, target: string = "employee", timeType: string = 'instant', permanence: string = 'temporary') {
    this.duration = duration
    this.target = target
    this.timeType = timeType
    this.permanence = permanence
  }
}
