<template>
  <template v-if="isLoaded">
      <div class="farm-window saves-card">
        <div class="stat-row">
          <h2 style="text-align: left;">Save Menu</h2>
          <p style="text-align: right;">Version {{ saveSystem.gameVersion }}</p>
        </div>
        <div class="save-selection-box">
          <div v-if="saveSystem.hasFarm"
               class="farm-window selected-save"
               :class="{'not-saved': !saveSystem.currentGameIsSaved, 'outdated-save': saveSystem.saveIsOutdated(farm.state.saveName)}">
            <input v-model="farm.state.saveName" style="grid-area: input;">
            <div style="grid-area: version; display: grid; grid-template-columns: 4fr 1fr;">
              <h3>
                <span v-if="saveSystem.saveIsOutdated(farm.state.saveName)">
                  OUTDATED <span class="unimportant">(saved {{ date(farm.state.saveDate) }})</span>
                </span>
                <span v-else-if="saveSystem.currentGameIsSaved">
                  Saved <span class="unimportant">({{ date(farm.state.saveDate) }})</span>
                </span>
                <span v-else>
                  Not saved <span class="unimportant">(since {{ date(farm.state.saveDate) }})</span>
                </span>
              </h3>
              <p style="margin-left: 1ch; text-align: right;">v{{ saveSystem.saveVersion(farm.state.saveName) }}</p>
            </div>
            <div class="save-info-detailed">
              <p><i>{{util().currentDate(farm.state.time)}}</i></p>
            </div>
            <TextButton @click="saveSystem.saveGame(); $forceUpdate()"
                        v-if="!saveSystem.saveIsOutdated(farm.state.saveName)"
                        class="farm-button"
                        style="grid-area: save;">
              <h3>Save</h3>
            </TextButton>
            <TextButton @click="saveSystem.deleteGame(); $forceUpdate()"
                        class="farm-button delete-save-button"
                        style="grid-area: delete; padding: 0;">
              <h3>Delete</h3>
            </TextButton>
          </div>
        </div>

        <div class="farm-list saves-list">
          <div class="flex-wrapper">
            <div v-for="(save, saveName) in saveSystem.allSaves" :key="saveName"
                 class="save-entry" :class="{'outdated-save': saveSystem.saveIsOutdated(saveName)}">
              <div class="save-info">
                <h3><span v-if="saveSystem.saveIsOutdated(saveName)">OUTDATED: </span>{{ saveName }}</h3>
                <p style="text-align: right; margin-right: 10px;">v{{ save.gameVersion }}</p>
                <p><i>{{util().currentDate(save.time)}}</i></p>
              </div>
              <TextButton @click="saveSystem.loadGame(saveName); $forceUpdate()"
                          class="load-button farm-button">
                <h3>Load</h3>
              </TextButton>
            </div>
            <TextButton @click="saveSystem.newGame(); $forceUpdate()"
                        class="farm-button new-game-button save-entry">
              <div class="new-game-button-internals">
                <h3>New Game</h3>
                <p style="font-size: 0.8em;">(Save current game first)</p>
              </div>
            </TextButton>
          </div>
        </div>
      </div>
  </template>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import Farm from '@/Farm'
import EmployeeCard from '@/components/EmployeeCard.vue'
import ValueMeter from '@/components/ValueMeter.vue'
import util from '../util'
import FarmStatusCard from '@/components/FarmStatusCard.vue'
import StatusBar from '@/components/StatusBar.vue'
import TextButton from '@/components/TextButton.vue'
import SaveSystem from '@/SaveSystem'

@Options({
  components: {
    TextButton
  },
  props: {
    saveSystem: Object
  },
  methods: {
    log(msg: any) {
      console.log(msg)
    },
    util() {
      return util
    },
    date(unixTime: number) {
      let date = new Date(unixTime)
      return date.toLocaleString('sv-SE', { hour12: false, year: 'numeric', month: 'numeric', day: 'numeric', hour: '2-digit', minute: '2-digit' })
    },
    window() {
      return window
    }
  },
  computed: {
    farm: function (): Farm {
      return this.saveSystem.farm
    }
  },
  async created() {
    this.isLoaded = true
  }
})
export default class SavesCard extends Vue {
  isLoaded = false
  saveSystem!: SaveSystem
  selectedSaveName = ''
}
</script>

